  padding: 4em 0;
}

#main > header {
  text-align: center;
  margin: 0 0 3em 0;
}

#main > header h2 {
  font-size: 2.75em;
  margin: 0;
}

#main > header p {
  border-top: solid 2px #e5e5e5;
  color: #777;
  display: inline-block;
  font-style: normal;
  margin: 1em 0 0 0;
  padding: 1em 0 1.25em 0;
}

body.landing #main {
  margin-top: -14em;
}

/* Footer */
#footer {
  background: #f0f0f0;
  padding: 4em 0 6em 0;
  text-align: center;
}

#footer .icons a {
  -moz-transition: opacity 0.2s ease-in-out;
  -webkit-transition: opacity 0.2s ease-in-out;
  -ms-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
  opacity: 0.35;
}

#footer .icons a:hover {
  opacity: 0.75;
}

#footer .copyright {
  color: #999;
  font-size: 0.9em;
  line-height: 1em;
  margin: 2em 0 0 0;
  padding: 0;
  text-align: center;
}

#footer .copyright a {
  color: inherit;
}

#footer .copyright li {
  border-left: solid 1px #dddddd;
  display: inline-block;
  list-style: none;
  margin-left: 1em;
  padding-left: 1em;
}

#footer .copyright li:first-child {
  border-left: 0;
  margin-left: 0;
  padding-left: 0;
}

/* CTA */
#cta {
  background: #e89980;
  color: #f9e6df;
  padding: 3.5em 0 4em 0;
  text-align: center;
}

#cta h2, #cta h3, #cta h4, #cta h5, #cta h6 {
  color: inherit;
}

#cta form {
  margin: 0 auto;
  max-width: 100%;
  width: 25em;
}

#cta input[type="submit"],
#cta input[type="reset"],
#cta input[type="button"],
#cta .button {
  box-shadow: none;
  background: #fff;
  color: #e89980;
}

#cta input[type="text"],
#cta input[type="password"],
#cta input[type="email"],
#cta select,
#cta textarea {
  color: #ffffff;
  background: rgba(255, 255, 255, 0.15);
  border: 0;
}

#cta input[type="text"]:focus,
#cta input[type="password"]:focus,
#cta input[type="email"]:focus,
#cta select:focus,
#cta textarea:focus {
  box-shadow: inset 0 0 0 2px #fff;
}

#cta ::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.75) !important;
}

#cta :-moz-placeholder {
  color: rgba(255, 255, 255, 0.75) !important;
}

#cta ::-moz-placeholder {
  color: rgba(255, 255, 255, 0.75) !important;
}

#cta :-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.75) !important;
}

#cta .formerize-placeholder {
  color: rgba(255, 255, 255, 0.75) !important;
}

/* Wide */
@media screen and (max-width: 1680px) {
  /* Basic */
  body, input, select, textarea {
    font-size: 13pt;
  }
  /* Banner */
  #banner {
    padding: 10em 0 18em 0;
  }
}

/* Normal */
@media screen and (max-width: 1280px) {
  /* Basic */
  body, input, select, textarea {
    font-size: 11pt;
  }
  /* Header */
  .dropotron.level-0 {
    font-size: 1em;
  }
  /* Banner */
  #banner {
    background-attachment: scroll;
  }
  #banner h2 {
    font-size: 3.5em;
  }
}

/* Narrow */
@media screen and (max-width: 980px) {
  /* Basic */
  body, input, select, textarea {
    font-size: 11pt;
  }
}

/* Narrower */
#navPanel, #navButton {
  display: none;
}

@media screen and (max-width: 840px) {
  /* Basic */
  html, body {
    overflow-x: hidden;
  }
  body, input, select, textarea {
    font-size: 11pt;
  }
  h2 br, h3 br, h4 br, h5 br, h6 br {
    display: none;
  }
  /* Section/Article */
  header br {
    display: none;
  }
  header.major {
    padding: 0 4em;
  }
  /* Box */
  .box {
    padding: 3em 2em;
  }
  .box.features > section {
    padding: 3em 2em;
  }
  .box .image.featured {
    margin-left: -2em;
    width: calc(100% + 4em);
  }
  .box .image.featured:first-child {
    margin-bottom: 3em;
    margin-top: -3em;
  }
  .box .image.featured:last-child {
    margin-bottom: -3em;
    margin-top: 3em;
  }
  /* Header */
  #page-wrapper {
    padding-top: 0;
  }
  #header {
    display: none;
  }
  /* Banner */
  /* Off-Canvas Navigation */
  #page-wrapper {
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
    -moz-transition: -moz-transform 0.5s ease;
    -webkit-transition: -webkit-transform 0.5s ease;
    -ms-transition: -ms-transform 0.5s ease;
    transition: transform 0.5s ease;
    padding-bottom: 1px;
  }
  #navButton {
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
    -moz-transition: -moz-transform 0.5s ease;
    -webkit-transition: -webkit-transform 0.5s ease;
    -ms-transition: -ms-transform 0.5s ease;
    transition: transform 0.5s ease;
    display: block;
    height: 44px;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10001;
  }
  #navButton .toggle {
    text-decoration: none;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    border: 0;
    outline: 0;
  }
  #navButton .toggle:before {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    text-transform: none !important;
  }
  #navButton .toggle:before {
    background: rgba(192, 192, 192, 0.75);
    border-radius: 6px;
    color: #fff;
    content: '\f0c9';
    display: block;
    font-size: 16px;
    height: 2.25em;
    left: 0.5em;
    line-height: 2.25em;
    position: absolute;
    text-align: center;
    top: 0.5em;
    width: 3.5em;
  }
  #navPanel {
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    backface-visibility: hidden;
    -moz-transform: translateX(-275px);
    -webkit-transform: translateX(-275px);
    -ms-transform: translateX(-275px);
    transform: translateX(-275px);
    -moz-transition: -moz-transform 0.5s ease;
    -webkit-transition: -webkit-transform 0.5s ease;
    -ms-transition: -ms-transform 0.5s ease;
    transition: transform 0.5s ease;
    display: block;
    height: 100%;
    left: 0;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 275px;
    z-index: 10002;
    background: #2b2b2b;
    color: #bbb;
  }
  #navPanel .link {
    border-bottom: 0;
    border-top: solid 1px rgba(255, 255, 255, 0.05);
    color: #bbb;
    display: block;
    height: 44px;
    line-height: 44px;
    padding: 0 1em 0 1em;
    text-decoration: none;
  }
  #navPanel .link:first-child {
    border-top: 0;
  }
  #navPanel .link.depth-0 {
    color: #fff;
  }
  #navPanel .link .indent-1 {
    display: inline-block;
    width: 1em;
  }
  #navPanel .link .indent-2 {
    display: inline-block;
    width: 2em;
  }
  #navPanel .link .indent-3 {
    display: inline-block;
    width: 3em;
  }
  #navPanel .link .indent-4 {
    display: inline-block;
    width: 4em;
  }
  #navPanel .link .indent-5 {
    display: inline-block;
    width: 5em;
  }
  body.navPanel-visible #page-wrapper {
    -moz-transform: translateX(275px);
    -webkit-transform: translateX(275px);
    -ms-transform: translateX(275px);
    transform: translateX(275px);
  }
  body.navPanel-visible #navButton {
    -moz-transform: translateX(275px);
    -webkit-transform: translateX(275px);
    -ms-transform: translateX(275px);
    transform: translateX(275px);
  }
  body.navPanel-visible #navPanel {
    -moz-transform: translateX(0);
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
}

/* Mobile */
@media screen and (max-width: 736px) {
  /* Basic */
  body, input, select, textarea {
    font-size: 11pt;
  }
  h2 {
    font-size: 1.75em;
    line-height: 1.35em;
    letter-spacing: -0.025em;
  }
  h3 {
    font-size: 1.5em;
  }
  h4 {
    font-size: 1em;
  }
  /* Section/Article */
  header.major {
    padding: 1em;
  }
  header.major h2, header.major p {
    padding-left: 0.5em;
    padding-right: 0.5em;
  }
  /* Box */
  .box {
    margin: 1em;
    overflow-x: hidden;
    padding: 2em 2em !important;
  }
  .box.features .features-row {
    border-top: 0;
    padding: 0;
  }
  .box.features .features-row section {
    border: 0;
    border-top: solid 1px #e5e5e5 !important;
    float: none;
    margin: 2em 0 0 0 !important;
    padding: 2em 0 0 0 !important;
    width: 100%;
  }
  .box.features .features-row:first-child section:first-child {
    border-top: 0 !important;
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  .box .image.featured {
    margin-left: -2em;
    width: calc(100% + 4em);
  }
  .box .image.featured:first-child {
    margin-bottom: 2em;
    margin-top: -2em;
  }
  .box .image.featured:last-child {
    margin-bottom: -2em;
    margin-top: 2em;
  }
  /* Banner */
  #banner {
    padding: 4em 0;
  }
  #banner h2 {
    font-size: 2.25em;
  }
  #banner p {
    font-size: 1.25em;
  }
  /* Main */
  #main {
    padding: 4em 0 0 0;
  }
  #main > header {
    margin: 0 2em 1.5em 2em;
  }
  #main > header h2 {
    font-size: 2em;
  }
  #main > header p {
    font-size: 1em;
    padding-bottom: 1em;
  }
  body.landing #main {
    padding: 0;
    margin-top: 0;
  }
  /* Footer */
  #footer {
    padding: 4em 0;
  }
  #footer .copyright li {
    border-left: 0;
    display: block;
    line-height: 2em;
    margin-left: 0;
    padding-left: 0;
  }
}

/* Mobile (Portrait) */
@media screen and (max-width: 480px) {
  /* Basic */
  html, body {
    min-width: 320px;
  }
  body, input, select, textarea {
    font-size: 11pt;
  }
  /* Section/Article */
  header.major {
    padding: 0;
  }
  /* List */
  ul.actions {
    margin: 0 0 2em 0;
  }
  ul.actions li {
    display: block;
    padding: 1em 0 0 0;
    text-align: center;
    width: 100%;
  }
  ul.actions li:first-child {
    padding-top: 0;
  }
  ul.actions li > * {
    width: 100%;
    margin: 0 !important;
  }
  ul.actions li > *.icon:before {
    margin-left: -2em;
  }
  ul.actions.small li {
    padding: 0.5em 0 0 0;
  }
  ul.actions.small li:first-child {
    padding-top: 0;
  }
  /* Box */
  .box {
    border-radius: 0;
    box-shadow: none;
    margin: 1em 0 0 0;
    padding: 3em 1em !important;
  }
  .box.features .features-row section {
    margin: 3em 0 0 0 !important;
    padding: 3em 0 0 0 !important;
  }
  .box .image.featured {
    border-radius: 0;
    margin-left: -1em;
    width: calc(100% + 2em);
  }
  .box .image.featured img {
    border-radius: 0 !important;
  }
  .box .image.featured:first-child {
    margin-bottom: 3em;
    margin-top: -3em;
  }
  .box .image.featured:last-child {
    margin-bottom: -3em;
    margin-top: 3em;
  }
  /* Banner */
  #banner {
    padding: 5em 1em 4em 1em;
  }
  /* Main */
  #main > .box:first-child {
    margin-top: 0;
  }
  /* CTA */
  #cta {
    padding: 2.5em 1em 3em 1em;
  }
}
